/*
Simple header component
Uses: MUI ButtonGroup
https://mui.com/components/button-group/
*/

import React from "react";
import { ButtonGroup, Button, Link } from "@mui/material";

import { MotionDown } from "../";
import { color } from "../../utils";
import "./Header.css";

// STYLES
const root = {
	display: "flex",
	justifyContent: "center",
	paddingTop: "20px",
};
const btn = {
	borderColor: color.black,
	fontSize: "20px",
	borderRadius: "25px",
};
const link = {
	color: color.black,
};

const Header = () => {
	return (
		<header>
			<div style={root}>
				<MotionDown>
					<ButtonGroup
						size="large"
						variant="outlined"
						aria-label="outlined button group"
					>
						<Button sx={btn}>
							<Link href="#Verkefni" underline="none" sx={link}>
								Verkefni
							</Link>
						</Button>
						<Button sx={btn}>
							<Link href="#Namskeid" underline="none" sx={link}>
								Námskeið
							</Link>
						</Button>
					</ButtonGroup>
				</MotionDown>
			</div>
		</header>
	);
};

export default Header;
